import * as React from 'react';
import '../App.css';
import { _ } from '../i18n';

interface NewUploadFileState {
    filename: string;
    version: string;
    file: File | null;
}
export class NewUpdateFile
    extends React.Component<{}, NewUploadFileState> {
    constructor(props: {}) {
        super(props);
        this.state = {
            filename: '',
            version: '',
            file: null
        };

        this.handleFilenameChange = this.handleFilenameChange.bind(this);
        this.handleVersionChange = this.handleVersionChange.bind(this);
        this.fileChangedHandler = this.fileChangedHandler.bind(this);
    }

    handleFilenameChange(event: React.FormEvent<HTMLInputElement>) {
      this.setState({filename: event.currentTarget.value});
    }

    handleVersionChange(event: React.FormEvent<HTMLInputElement>) {
      this.setState({version: event.currentTarget.value});
    }

    fileChangedHandler(selectorFiles: FileList | null) {
      if (selectorFiles) {
        this.setState({file: selectorFiles[0]});
      }
    }

    render() {
        return (
          <div>
            <h1>
              {_('newUpdatefile')}
            </h1>

            <form 
              className="new-update-form" 
              encType="multipart/form-data" 
              method="POST" 
              action="http://dev.jouko.fi:8080/fileUpload"
            >
              <h5>
                {_('filename')}
              </h5>

              <input
                type="text"
                name="filename"
                value={this.state.filename}
                onChange={this.handleFilenameChange}
              />

              <h5>
                {_('version')}
              </h5>

              <input
                type="text"
                name="version"
                value={this.state.version}
                onChange={this.handleVersionChange}
              />

              <h5>
                {_('file')}
              </h5>

              <input
                type="file"
                name="file"
                onChange={(e) => this.fileChangedHandler(e.target.files)}
              />

              <div className="ActionField">
                  <input type="submit" value={_('newUpdatefile')} />
              </div>
            </form>
          </div>
        );
    }
}


